/**
 * @module math
 * 数学计算工具类
 */

/**
 * 获取随机整数数组
 * @param   {Number} min 最小值
 * @param   {Number} max 最大值
 * @param   {Number} n 数组长度
 * @return  {Array} 随机整数数组
 */
export const randomIntArrayInRange = (
  min: number,
  max: number,
  n: number = 1
): number[] =>
  Array.from(
    { length: n },
    () => Math.floor(Math.random() * (max - min + 1)) + min
  );
